* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  background-color: gainsboro;
  height: 100%;
}

html,
#root,
.container {
  height: 100%;
  display: flex;
  justify-content: center;
}

.loader {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 400;
}

.movies {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  padding: 20px;
  padding-top: 100px;
  width: 80%;
}

.movies .movie {
  width: 45%;
  background-color: whitesmoke;
  margin-bottom: 2.5rem;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  padding: 20px;
  color: #525252;
  border-radius: 10px;
  box-shadow: 0 50px 100px -20px rgba(50, 50, 93, 0.25),
    0 30px 60px -30px rgba(0, 0, 0, 0.3);
}

.movie__image {
  width: 100%;
  max-width: 10rem;
  margin-right: 20px;
  box-shadow: 0 13px 27px -5px rgba(0, 0, 0, 0.3);
}

.movie__title,
.movie__year {
  margin: 0;
}

.movie__title {
  margin-bottom: 10px;
  font-size: 1.2rem;
  font-weight: 600;
  color: #3b3b3b;
}

.movie__year,
.movie__genres {
  opacity: 0.5;
  font-weight: 400;
  font-size: 0.7rem;
}

.movie__genres {
  list-style: none;
  display: flex;
  padding: 0;
  margin: 0;
  margin-top: 4px;
  font-style: italic;
  color: seagreen;
}

.genres__genre {
  margin-right: 8px;
}

.movie__summary {
  font-size: 0.9rem;
}
